<template>
    <div class="moviedetail">
        <!-- 头部 -->
        <div class="detail-header" >
            <mt-header 
                fixed 
                :class="{active:fixFlag}"  
            >
                <router-link to="/" slot="left">
                    <mt-button icon="back" :class="{after:fixFlag}"></mt-button>
                </router-link>
            </mt-header>
        </div>
        <!-- 头部结束 -->
        <div class="img-box">
            <img width="100%" height="210px" :src="movie.poster" alt="">
        </div>
        <div class="detail">
            <dl>
                <dt>
                    <h3>{{movie.name}} <span v-if="movie.filmType">{{movie.filmType.name}}</span></h3>
                    <p>{{movie.grade}}分</p>
                </dt>
                <dd>
                    <p>{{movie.category}}</p>
                    <p>2019.09.30上映</p>
                    <p>{{movie.nation}} | {{movie.runtime}}</p>
                </dd>
                <dd>
                    <p>{{movie.synopsis}}</p>
                </dd>
            </dl>
        </div>
        <div class="actors">
            <h3>演职人员</h3>
            <div class="move">
                <!-- 演职banner -->
                <div class="swiper-container">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide"
                            v-for="actor in movie.actors"
                            :key="actor.index"
                        >
                            <dl class="move-dl">
                                <dt>
                                    <img :src="actor.avatarAddress" alt="">
                                </dt>
                                <dd>{{actor.name}}</dd>
                                <dd>{{actor.role}}</dd>
                            </dl>
                        </div>
                    </div>
                </div>
                <!-- 演职banner -->
                <!-- <dl class="move-dl">
                    <dt>
                        <img src="" alt="">
                    </dt>
                    <dd>{{actor.name}}</dd>
                    <dd>导演</dd>
                </dl> -->
            </div>
        </div>
        <div class="stage">
            <h3>剧照 <router-link to="" tag="a">全部(7)></router-link></h3>
            <!-- 演职banner -->
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div 
                        class="swiper-slide"
                        v-for="photo in movie.photos"
                        :key="photo.index"
                    >
                        <dl class="stage-dl">
                            <dt v-if="photo">
                                <img width="150px" heigth="100px" :src="photo" alt="">
                            </dt>
                        </dl>
                    </div>
                </div>
            </div>
        </div>
        <router-link 
            to="" 
            class="choose"
        >
                选座购票
        </router-link>
    </div>
</template>

<script>
import Swiper from "swiper"
export default {
    data(){
        return{
            movies:null,
            movie:Object,
            actor:Object,
            photo:String,
           /*  i:Number, */
            flags: false,
            fixFlag:false,
           
            
        }
    },
    created(){
        let movieId = this.$route.params.id
        let types = this.$route.params.types
        /* console.log(types) */
        this.$http.get("./api/mz/movie/"+types,{
              params:{
                    limit:20,
                }
        }).then(res=>{
            /* console.log(res.data.data.object_list) */
            this.movies = res.data.data.object_list
            for(let i =0; i< this.movies.length;i++){
                if(this.movies[i].filmId == movieId){
                    /* console.log(this.movies[i]) */
                    this.movie = this.movies[i]
                }
            }
            //遍历演员
           this.movie.actors.forEach((item,index,arr)=>{
                /* console.log(item,index) */
                this.actor = item 
                /* this.i = index */
            })
            //遍历剧照
            this.movie.photos.forEach((item,index)=>{
                /* console.log(item,index) */
                this.photo = item
            })
            this.$nextTick(()=>{
                new Swiper(".swiper-container",{
                    /* slidesPerView: 4,
                    spaceBetween: 30,
                    centeredSlides: true, */
                    slidesPerView: 3,
                    spaceBetween: 30
                })
            })
        })  
        window.addEventListener("scroll",()=>{
            let sTop = document.documentElement.scrollTop || document.body.scrollTop;
            /* console.log(sTop) */
            if(sTop >= 46 && !this.fixFlag){
                this.fixFlag = true
            }else if(sTop < 46 && this.fixFlag){
                this.fixFlag = false
            }
        })
    },
    methods:{
        
        
    }
    
}
</script>

<style lang="scss">
    .moviedetail{
        position:relative;
        background:#f4f4f4;
        padding-bottom:60px;
        .mint-header{
            background: transparent;
            &.active{
            background: #fff;
            }
            .mint-button{
                width:0.3rem;
                height:0.3rem;
                border-radius:50%;
                background: rgba(255,255,255,.5);
                &.after{
                    content:"";
                    width:0.3rem;
                    height:0.3rem;
                    border-radius:50%;
                    color:#000;
                }
            }
        }
        
        .detail{
            background: #fff;
            dl{
                padding: 15px;
                dt{
                    display:flex;
                    justify-content:space-between;
                    align-items:center;
                    h3{
                        line-height:14px;
                        height:14px;
                        color:#191a1b;
                        font-size: 18px;
                        height: 24px;
                        line-height: 24px;
                        span{
                            font-size: 9px;
                            color: #fff;
                            background-color: #d2d6dc;
                            height: 14px;
                            line-height: 14px;
                            padding: 0 2px;
                            border-radius: 2px;
                            display: inline-block;
                        }
                    }
                    p{
                        color:#ffb232;
                        font-size: 18px;
                        font-style: italic;
                    }
                }
                dd:nth-of-type(1){
                    font-size: 13px;
                    color: #797d82;
                    margin-top: 4px;
                    p{
                        margin-top: 4px;
                    }
                }
                dd:nth-of-type(2){
                    /* overflow: hidden; */
                    margin-top:12px;
                    /* font-size: 13px;
                    color: #797d82; */
                    p{
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        font-size: 13px;
                        color: #797d82;
                    }
                }
            }
        }
        .actors{
            margin-top:10px;
            background: #fff;
            padding-bottom:20px;
            position:relative;
            width:3.75rem;
            height:1.93rem;
            h3{
                font-size: 16px;
                text-align: left;
                color: #191a1b;
                padding:15px;
            }
            .move{
                height: 140px;
                background: #fff;
                .swiper-slide{
                    width: 85px!important;
                    margin-right:10px!important;
                    width:85px;
                        dl{
                            width:85px;
                            height:130px;
                        dt{
                            width: 85px;
                            height: 85px;
                            background: rgb(249, 249, 249);
                            opacity: 1;
                            img{
                                width: 85px;
                                height: 85px;
                            }
                        }
                        dd{
                            padding-top: 10px;
                            font-size: 12px;
                            color: #191a1b;
                            width: 85px;
                            height: 18px;
                            text-align:center;
                            line-height:18px;
                        }:nth-of-type(2){
                            color:  #797d82;
                            font-size: 10px;
                        }
                    }

                }
                
            }
           
        }
        .stage{
            margin-top:12px;
            background: #fff;
            h3{
                position:relative;
                font-size: 16px;
                text-align: left;
                color: #191a1b;
                padding:15px;
                a{
                    position:absolute;
                    right:10px;
                }
            }
            .swiper-wrapper{
                transition-duration: 0ms;
                transform: translate3d(0px, 0px, 0px);
                .swiper-slide{
                    width:150px!important;
                    height:100px!important;
                    margin-right: 8px!important;
                    .stage-dl {
                        dt{
                            img{
                                width:150px;
                                height:100px;
                            }
                        }
                    }
                }
            }
        }
        .choose{
            position:fixed;
            bottom:0;
            left:0;
            background: #ff5f16;
            width:100%;
            height:50px;
            text-align:center;
            line-height:50px;    
            color:#fff;
            font-size:16px;
            z-index:5;
        }
    }
</style>